import { assestsUserFinanceUrl } from "../../../../../Services/UrlConfig/AssestsBaseUrl";
import { imageShowUrl } from "../../../../../Services/UrlConfig/BaseUrl";
import style from "../../../StoreManager.module.css";

const SelectClassUnitCard = ({ cls, onSelect, selectedId }) => {
  const onClick = () => {
    onSelect(cls);
  };
  return (
    <div
      className={
        selectedId?.includes(cls?._id)
          ? `${style.smc_card} ${style.smc_card_active}`
          : style.smc_card
      }
      onClick={onClick}
    >
      <div
        className={style.smc_card_contatiner}
        style={{
          justifyContent: "space-between",
          width: "100%",
        }}
      >
        <div className={style.smc_card_contatiner}>
          <img
            src={
              cls?.dHead?.staffProfilePhoto
                ? `${imageShowUrl}/${cls?.dHead?.staffProfilePhoto}`
                : `${assestsUserFinanceUrl}/department.svg`
            }
            loading="lazy"
            alt="avatar"
            style={{
              height: "2.3rem",
              width: "2.3rem",
              borderRadius: "50%",
            }}
          />

          <div className={style.smc_card_contatiner_inner}>
            <h6>
              {cls?.className ?? ""} - {cls?.classTitle ?? ""}
            </h6>
            <p>
              {`${cls?.dHead?.staffFirstName ?? ""} ${
                cls?.dHead?.staffMiddleName ?? ""
              } ${cls?.dHead?.staffLastName ?? ""}`}
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SelectClassUnitCard;
