import { useLocation, useSearchParams } from "react-router-dom";
import StoreMaster from "./StoreMaster";
import StoreMasterCategory from "./StoreMasterCategory/StoreMasterCategory";

const StoreMasterConfig = () => {
  const [searchParams] = useSearchParams();
  const getQuery = useLocation();
  return (
    <>
      {searchParams.get("n") === "category" ? (
        <StoreMasterCategory />
      ) : (
        <StoreMaster stid={getQuery.state?.stid} />
      )}
    </>
  );
};

export default StoreMasterConfig;
