import { assestsUserFinanceUrl } from "../../../../../Services/UrlConfig/AssestsBaseUrl";
import { imageShowUrl } from "../../../../../Services/UrlConfig/BaseUrl";
import CreateButton from "../../../../../Ui/Button/CreateButton";
import style from "../../../StoreManager.module.css";

const SelectDepartmentUnitCard = ({
  unit,
  selectedId,
  onSelect,
  setStep,
  dataState,
}) => {
  const onClick = () => {
    onSelect("Department", unit);
  };

  const onAction = (e) => {
    e.stopPropagation();
    dataState((prev) => ({
      ...prev,
      did: unit?._id,
    }));
    setStep(7);
  };
  return (
    <div
      className={
        selectedId?.includes(unit?._id)
          ? `${style.smc_card} ${style.smc_card_active}`
          : style.smc_card
      }
      onClick={onClick}
    >
      <div
        className={style.smc_card_contatiner}
        style={{
          justifyContent: "space-between",
          width: "100%",
        }}
      >
        <div className={style.smc_card_contatiner}>
          <img
            src={
              unit?.dHead?.staffProfilePhoto
                ? `${imageShowUrl}/${unit?.dHead?.staffProfilePhoto}`
                : `${assestsUserFinanceUrl}/department.svg`
            }
            loading="lazy"
            alt="avatar"
            style={{
              height: "2.3rem",
              width: "2.3rem",
              borderRadius: "50%",
            }}
          />

          <div className={style.smc_card_contatiner_inner}>
            <h6>{unit?.dName ?? ""}</h6>
            <p>
              {`${unit?.dHead?.staffFirstName ?? ""} ${
                unit?.dHead?.staffMiddleName ?? ""
              } ${unit?.dHead?.staffLastName ?? ""}`}
            </p>
          </div>
        </div>
        <CreateButton
          label="select_class"
          customStyle={{
            paddingBlock: "0.4rem",
            margin: "0",
          }}
          onAction={onAction}
        />
      </div>
    </div>
  );
};

export default SelectDepartmentUnitCard;
