import { useSearchParams } from "react-router-dom";
import StoreStock from "./StoreStock";
import StoreStockSubCategory from "./StoreStockSubCategory";
// import StoreStockLog from "./StoreStockLog/StoreStockLog";

const StoreStockConfig = ({ stid }) => {
  const [searchParams] = useSearchParams();
  return (
    <>
      {searchParams.get("n") === "category" ? (
        <StoreStockSubCategory />
      ) : (
        //  searchParams.get("n") === "logs" ? (
        //   <StoreStockLog />
        // ) :
        <StoreStock stid={stid} />
      )}
    </>
  );
};

export default StoreStockConfig;
