import { useCallback, useEffect, useState } from "react";
import Loading from "../../../../Loader/Loading";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import style from "../../../FinanceManager/FinanceManager.module.css";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import { useTranslation } from "react-i18next";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import CreateButton from "../../../../Ui/Button/CreateButton";
import StoreUnitDepartmentDetailCard from "./StoreUnitDepartmentDetailCard";
import { useStoreDepartmentClassList } from "../../../../Hooks/ApiHooks/StoreManager/store-manager-api";

const StoreUnitDepartmentDetail = () => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const { storeDepartmentClassList, storeDepartmentClassListLoading } =
    useStoreDepartmentClassList({
      data: {
        did: getQuery.state?.did,
        page: page,
        limit: 10,
        search: search,
      },
      skip: !getQuery.state?.did,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (storeDepartmentClassList?.list?.length) {
      let obj = nextPage(storeDepartmentClassList?.list?.length);
      setState(() => obj);
    }
  }, [storeDepartmentClassList?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onNavigate = () => {
    let url = `/q/${params.username}/member/staff/store?type=store-department`;
    navigate(url, {
      state: getQuery.state,
    });
  };

  return (
    <UserMemberContainerWrapper>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>{getQuery.state?.name ?? "Department Name"}</h6>
          <div className={style.fm_search}>
            <section className={style.fm_search_container}>
              <div className={style.fm_search_container_inner}>
                <img
                  className={style.fm_search_icon}
                  alt="search icon"
                  src={`${assestsNavbarUrl}/navbar-search.svg`}
                />
                <input
                  type="text"
                  placeholder={t("search")}
                  onChange={onSearchEvent}
                  style={{
                    minWidth: "250px",
                  }}
                />
              </div>
            </section>
            <CreateButton
              label="department_office_staff"
              customStyle={{
                paddingBlock: "0.4rem",
                margin: "0",
              }}
              onAction={onNavigate}
            />
          </div>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper>
        {storeDepartmentClassList?.list?.map((cls, index) =>
          storeDepartmentClassList?.list?.length === index + 1 ? (
            <div key={cls?._id} ref={ref}>
              <StoreUnitDepartmentDetailCard cls={cls} />
            </div>
          ) : (
            <StoreUnitDepartmentDetailCard cls={cls} key={cls?._id} />
          )
        )}
        {storeDepartmentClassListLoading ? (
          search ? (
            <QvipleLoader />
          ) : (
            <Loading />
          )
        ) : search ? (
          storeDepartmentClassList?.list?.length === 0 && (
            <EmptyMessage title="No classes found related this search." />
          )
        ) : (
          storeDepartmentClassList?.list?.length === 0 && (
            <EmptyMessage title="No classes." />
          )
        )}
      </UserMemberContentWrapper>
    </UserMemberContainerWrapper>
  );
};
export default StoreUnitDepartmentDetail;
