import { useState } from "react";
import { useTranslation } from "react-i18next";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../FinanceManager/Income/Income.module.css";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import CreateButton from "../../../../Ui/Button/CreateButton";
import { useStoreStockIssueRecord } from "../../../../Hooks/ApiHooks/StoreManager/store-manager-api";
import QvipleLoader from "../../../../Loader/QvipleLoader";

const StoreIssueRecordConfirm = ({ onParentClose, state, onClose, stid }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);

  const [storeStockIssueRecord] = useStoreStockIssueRecord();
  const onNext = () => {
    if (stid && state?.arr?.length && state?.flow && state?.assignee) {
      setDisabled((pre) => !pre);
      storeStockIssueRecord({
        stid: stid,
        bodyParameter: {
          arr: state?.arr,
          flow: state?.flow,
          assignee: state?.assignee,
        },
      })
        .then(() => {
          setDisabled((pre) => !pre);
          onParentClose();
        })
        .catch({});
    }
  };
  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("confirm_to_issue_record")}</h6>
              </div>

              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom
            customStyleBorder={{
              marginBottom: "0",
            }}
          />
          <UserMemberContentWrapper
            customStyle={{
              paddingBottom: "1rem",
            }}
          >
            <CreateButton label="issue_record" onAction={onNext} />
          </UserMemberContentWrapper>
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default StoreIssueRecordConfirm;
