import { useTranslation } from "react-i18next";
import { assestsFeedUrl } from "../../../../../Services/UrlConfig/AssestsBaseUrl";
import { imageShowUrl } from "../../../../../Services/UrlConfig/BaseUrl";
import style from "../../../StoreManager.module.css";

const SelectCustomUnitCard = ({ unit, selectedId, onSelect }) => {
  const { t } = useTranslation();
  const onClick = () => {
    onSelect("Custom", unit);
  };
  return (
    <div
      className={
        selectedId?.includes(unit?._id)
          ? `${style.smc_card} ${style.smc_card_active}`
          : style.smc_card
      }
      onClick={onClick}
    >
      <div
        className={style.smc_card_contatiner}
        style={{
          justifyContent: "space-between",
          width: "100%",
        }}
      >
        <div className={style.smc_card_contatiner}>
          <img
            src={
              unit?.good_head_person?.staffProfilePhoto
                ? `${imageShowUrl}/${unit?.good_head_person?.staffProfilePhoto}`
                : `${assestsFeedUrl}/feed-user-avatar.svg`
            }
            loading="lazy"
            alt="avatar"
            style={{
              height: "2.3rem",
              width: "2.3rem",
              borderRadius: "50%",
            }}
          />

          <div className={style.smc_card_contatiner_inner}>
            <h6>
              {t("good_title_person")} {": "} {unit?.good_title_person ?? ""}
              {", "}
              {t("good_head_name")} {": "} {unit?.good_head_name ?? ""}
            </h6>

            <p>
              {`${unit?.good_head_person?.staffFirstName ?? ""} ${
                unit?.good_head_person?.staffMiddleName ?? ""
              } ${unit?.good_head_person?.staffLastName ?? ""}`}
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SelectCustomUnitCard;
