import { Link, useLocation, useParams } from "react-router-dom";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../StoreManager.module.css";
import { useTranslation } from "react-i18next";
const StoreStockSubCategoryCard = ({ subCategory }) => {
  const { t } = useTranslation();
  const params = useParams();
  const getQuery = useLocation();
  return (
    <>
      <Link
        className={style.smc_card}
        to={`/q/${params.username}/member/staff/store?type=store-stock`}
        state={{
          ...getQuery.state,
          goods: subCategory,
        }}
      >
        <div className={style.smc_card_contatiner}>
          <img
            src={
              subCategory?.good_icon
                ? `${imageShowUrl}/${subCategory?.good_icon}`
                : `${assestsUserFinanceUrl}/category.svg`
            }
            alt="categor good_icon"
          />
          <div className={style.smc_card_contatiner_inner}>
            <h6>
              {t("name")}
              {": "}
              {subCategory?.goods_name ?? ""}
            </h6>
            <p>
              {t("measures")} {": "} {subCategory?.goods_quantity ?? ""}
            </p>
            <p>
              {t("remaining_quantity")} {": "} {subCategory?.goods_volume ?? ""}
            </p>
          </div>
        </div>
      </Link>
    </>
  );
};

export default StoreStockSubCategoryCard;
