import { Fragment, useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import Modal from "../../../../Ui/Modal/Modal";
import style from "../../../PlateformUser/PlateformUser.module.css";
import { useStoreUnitCombineList } from "../../../../Hooks/ApiHooks/StoreManager/store-manager-api";
import SelectCustomUnitCard from "./IssueUnit/SelectCustomUnitCard";
import SelectDepartmentUnitCard from "./IssueUnit/SelectDepartmentUnitCard";
const SelectStoreUnit = ({
  onClose,
  stid,
  onSelectUnit,
  selectedId,
  setStep,
  dataState,
}) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const { storeUnitCombineList, storeUnitCombineListLoading } =
    useStoreUnitCombineList({
      data: {
        stid: stid,
        page: page,
        limit: 10,
        search: search,
      },
      skip: !stid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (storeUnitCombineList?.list?.length) {
      let obj = nextPage(storeUnitCombineList?.list?.length);
      setState(() => obj);
    }
  }, [storeUnitCombineList?.list?.length]);
  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onSelect = (flow, data) => {
    onSelectUnit(flow, data);
  };
  return (
    <Modal onClose={onClose}>
      <div className={style.modal_container}>
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>{t("select_unit_or_department")}</h6>
            </div>
            <img
              src={`${assestsUserFinanceUrl}/close.svg`}
              alt="close icon"
              onClick={onClose}
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom
          customStyleBorder={{
            marginBottom: "0",
          }}
        />
        <UserMemberContentWrapper
          customStyle={{
            paddingBottom: "1rem",
          }}
        >
          <div
            className={style.assign_search_container}
            style={{
              marginBottom: "1rem",
            }}
          >
            <input
              type="text"
              placeholder={t("search")}
              className={style.assign_search_input}
              onChange={onSearchEvent}
              style={{
                width: "100%",
              }}
            />
            <img
              src={`${assestsNavbarUrl}/navbar-search.svg`}
              alt="search icon"
            />
          </div>

          {storeUnitCombineList?.list?.map((unit, index) =>
            storeUnitCombineList?.list?.length === index + 1 ? (
              <div key={unit?._id} ref={ref}>
                {unit?.dName ? (
                  <SelectDepartmentUnitCard
                    unit={unit}
                    onSelect={onSelect}
                    selectedId={selectedId}
                    setStep={setStep}
                    dataState={dataState}
                  />
                ) : (
                  <SelectCustomUnitCard
                    unit={unit}
                    onSelect={onSelect}
                    selectedId={selectedId}
                  />
                )}
              </div>
            ) : (
              <Fragment key={unit?._id}>
                {unit?.dName ? (
                  <SelectDepartmentUnitCard
                    unit={unit}
                    onSelect={onSelect}
                    selectedId={selectedId}
                    setStep={setStep}
                    dataState={dataState}
                  />
                ) : (
                  <SelectCustomUnitCard
                    unit={unit}
                    onSelect={onSelect}
                    selectedId={selectedId}
                  />
                )}
              </Fragment>
            )
          )}
          {storeUnitCombineListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            storeUnitCombineList?.list?.length === 0 && (
              <EmptyMessage
                title="No units found related this search."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          ) : (
            storeUnitCombineList?.list?.length === 0 && (
              <EmptyMessage
                title="No units."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          )}
        </UserMemberContentWrapper>
      </div>
    </Modal>
  );
};

export default SelectStoreUnit;
