import { useParams, useSearchParams } from "react-router-dom";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import HorizontalTabCard from "../../../Ui/Tab/Horizontal/HorizontalTabCard";
import HorizontalTabWrapper from "../../../Ui/Tab/Horizontal/HorizontalTabWrapper";

const StoreIssueWrapper = ({ parentState }) => {
  const params = useParams();
  const [searchParams] = useSearchParams();
  return (
    <>
      <>
        <HorizontalTabWrapper>
          {tabList.map((tab) => (
            <HorizontalTabCard
              key={tab?.id}
              tab={tab}
              isLink
              isActive={tab.groupIs?.includes(searchParams.get("n"))}
              parentState={parentState}
              url={`/q/${params.username}/member/staff/store?type=issue${tab.urlKey}`}
            />
          ))}
        </HorizontalTabWrapper>
        <BorderBottom
          customStyleBorder={{
            marginBlock: "0",
          }}
        />
      </>
    </>
  );
};

export default StoreIssueWrapper;
const tabList = [
  {
    label: "request",
    id: "request",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "issue_record",
    id: "records",
    urlKey: "&n=records",
    groupIs: ["records"],
  },
];
