import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { useLocation } from "react-router-dom";
import { useStoreMasterSubCategoryList } from "../../../Hooks/ApiHooks/StoreManager/store-manager-api";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import StoreStockSubCategoryCard from "./StoreStockSubCategoryCard";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../FinanceManager/FinanceManager.module.css";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
const StoreStockSubCategory = () => {
  const getQuery = useLocation();
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const { storeMasterSubCategoryList, storeMasterSubCategoryListLoading } =
    useStoreMasterSubCategoryList({
      data: {
        cid: getQuery.state?.category?._id,
        page: page,
        limit: 10,
        search: search,
      },
      skip: !getQuery.state?.category?._id,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (storeMasterSubCategoryList?.list?.length) {
      let obj = nextPage(storeMasterSubCategoryList?.list?.length);
      setState(() => obj);
    }
  }, [storeMasterSubCategoryList?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>
              {getQuery.state?.category?.category_name ?? ""} {t("stock")}
            </h6>
            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                  />
                </div>
              </section>
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          {storeMasterSubCategoryList?.list?.map((subCategory, index) =>
            storeMasterSubCategoryList?.list?.length === index + 1 ? (
              <div key={subCategory?._id} ref={ref}>
                <StoreStockSubCategoryCard subCategory={subCategory} />
              </div>
            ) : (
              <StoreStockSubCategoryCard
                subCategory={subCategory}
                key={subCategory?._id}
              />
            )
          )}
          {storeMasterSubCategoryListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            storeMasterSubCategoryList?.list?.length === 0 && (
              <EmptyMessage title="No sub-category found related this search." />
            )
          ) : (
            storeMasterSubCategoryList?.list?.length === 0 && (
              <EmptyMessage title="No sub-category." />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
    </>
  );
};

export default StoreStockSubCategory;
