import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import style from "../../FinanceManager/FinanceManager.module.css";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../../Ui/Button/CreateButton";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import AddStoreMaster from "./AddStoreMaster";
import StoreMasterCard from "./StoreMasterCard";
import { useStoreMasterCategoryList } from "../../../Hooks/ApiHooks/StoreManager/store-manager-api";
const StoreMaster = ({ stid }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const [addCategory, setAddCategory] = useState(false);

  const {
    storeMasterCategoryList,
    storeMasterCategoryListLoading,
    storeMasterCategoryListRefetch,
  } = useStoreMasterCategoryList({
    data: {
      stid: stid,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !stid,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (storeMasterCategoryList?.list?.length) {
      let obj = nextPage(storeMasterCategoryList?.list?.length);
      setState(() => obj);
    }
  }, [storeMasterCategoryList?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onToggle = () => {
    setAddCategory((pre) => !pre);
  };
  const onRefetch = () => {
    setPage(() => 1);
    setSearch("");
    storeMasterCategoryListRefetch();
  };

  return (
    <>
      <UserMemberContentWrapper
        customStyle={{
          padding: "0.8rem 0 0 0",
        }}
      >
        <div
          className={style.fm_search_full}
          style={{
            marginBottom: "0.5rem",
          }}
        >
          <section
            className={style.fm_search_full_container}
            style={{
              gap: "1rem",
            }}
          >
            <div
              className={style.fm_search_full_container_inner}
              style={{
                flex: "1.5 1.5",
              }}
            >
              <img
                className={style.fm_search_full_icon}
                alt="search icon"
                src={`${assestsNavbarUrl}/navbar-search.svg`}
              />
              <input
                type="text"
                placeholder={t("search")}
                onChange={onSearchEvent}
              />
            </div>
            <CreateButton
              label="add_new_category"
              customStyle={{
                paddingBlock: "0.4rem",
                margin: "0",
              }}
              onAction={onToggle}
            />
          </section>
        </div>

        {storeMasterCategoryList?.list?.map((category, index) =>
          storeMasterCategoryList?.list?.length === index + 1 ? (
            <div key={category?._id} ref={ref}>
              <StoreMasterCard category={category} />
            </div>
          ) : (
            <StoreMasterCard category={category} key={category?._id} />
          )
        )}
        {storeMasterCategoryListLoading ? (
          search ? (
            <QvipleLoader />
          ) : (
            <Loading />
          )
        ) : search ? (
          storeMasterCategoryList?.list?.length === 0 && (
            <EmptyMessage title="No category found related this search." />
          )
        ) : (
          storeMasterCategoryList?.list?.length === 0 && (
            <EmptyMessage title="No category." />
          )
        )}

        {addCategory && (
          <AddStoreMaster
            onRefetch={onRefetch}
            onClose={onToggle}
            stid={stid}
          />
        )}
      </UserMemberContentWrapper>
    </>
  );
};

export default StoreMaster;
