import { useLocation, useSearchParams } from "react-router-dom";
import StoreUnit from "./StoreUnit";
import StoreUnitDepartment from "./StoreUnitDepartment/StoreUnitDepartment";
import StoreUnitDepartmentDetail from "./StoreUnitDepartmentDetail/StoreUnitDepartmentDetail";

const StoreUnitConfig = ({ instituteId, fid }) => {
  const [searchParams] = useSearchParams();
  const getQuery = useLocation();
  return (
    <>
      {searchParams.get("n") === "detail" ? (
        <StoreUnitDepartmentDetail />
      ) : searchParams.get("n") === "department" ? (
        <StoreUnitDepartment />
      ) : (
        <StoreUnit
          stid={getQuery.state?.stid}
          instituteId={instituteId}
          fid={fid}
        />
      )}
    </>
  );
};

export default StoreUnitConfig;
