import { useCallback, useEffect, useState } from "react";
import Loading from "../../../Loader/Loading";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import style from "../../FinanceManager/FinanceManager.module.css";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import { useTranslation } from "react-i18next";
import StoreUnitCard from "./StoreUnitCard";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useStoreUnitHeadPersonList } from "../../../Hooks/ApiHooks/StoreManager/store-manager-api";
import AddStoreUnit from "./AddStoreUnit";
import StoreUnitDepartment from "./StoreUnitDepartment/StoreUnitDepartment";
const StoreUnit = ({ stid, instituteId, fid }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const [add, setAdd] = useState(false);

  const {
    storeUnitHeadPersonList,
    storeUnitHeadPersonListLoading,
    storeUnitHeadPersonListRefetch,
  } = useStoreUnitHeadPersonList({
    data: {
      stid: stid,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !stid,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (storeUnitHeadPersonList?.list?.length) {
      let obj = nextPage(storeUnitHeadPersonList?.list?.length);
      setState(() => obj);
    }
  }, [storeUnitHeadPersonList?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onToggle = () => {
    setAdd((pre) => !pre);
  };

  const onRefetch = () => {
    setPage(() => 1);
    setSearch("");
    storeUnitHeadPersonListRefetch();
  };

  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("unit")}</h6>
            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                  />
                </div>
              </section>
              <CreateButton
                label="add"
                customStyle={{
                  paddingBlock: "0.4rem",
                  margin: "0",
                }}
                onAction={onToggle}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          {/* <StoreUnitCard
            subUrl="department"
            isStatic
            unit={{
              name: "departments",
            }}
          /> */}
          <StoreUnitDepartment fid={fid} />
          {storeUnitHeadPersonList?.list?.map((unit, index) =>
            storeUnitHeadPersonList?.list?.length === index + 1 ? (
              <div key={unit?._id} ref={ref}>
                <StoreUnitCard unit={unit} subUrl="custom" />
              </div>
            ) : (
              <StoreUnitCard unit={unit} key={unit?._id} subUrl="custom" />
            )
          )}
          {storeUnitHeadPersonListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            storeUnitHeadPersonList?.list?.length === 0 && (
              <EmptyMessage title="No unit found related this search." />
            )
          ) : (
            storeUnitHeadPersonList?.list?.length === 0 && (
              <EmptyMessage title="No unit." />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {add && (
        <AddStoreUnit
          stid={stid}
          onClose={onToggle}
          onRefetch={onRefetch}
          instituteId={instituteId}
        />
      )}
    </>
  );
};

export default StoreUnit;
