import { useState } from "react";
import IssueRecordToIssuing from "./IssueRecordToIssuing";
import InstituteStaff from "../../../InstituteStaff/InstituteStaff";
import MultiSelectGoods from "./MultiSelectGoods";
import SelectStoreUnit from "./SelectStoreUnit";
import SelectStoreDepartmentClass from "./SelectStoreDepartmentClass";
import StoreIssueRecordConfirm from "./StoreIssueRecordConfirm";
import IssueRecordForm from "./IssueRecordForm";

const StoreIssueRecord = ({ instituteId, stid, onClose }) => {
  const [step, setStep] = useState(1);
  const [state, setState] = useState({
    arr: [],
    assignee: "",
    flow: "",
    staff: "",
    issue_to: "",
    goods: [],
    goodsId: [],
    did: "",
  });
  const onSelectStaff = (val) => {
    setState((prev) => ({
      ...prev,
      staff: val,
      assignee: val?._id,
      flow: "Individual",
    }));
    setStep(3);
  };
  const onGoodsSelect = (data) => {
    setState((prev) => ({
      ...prev,
      goods: data?.goods,
      goodsId: data?.goodsId,
    }));
    setStep(4);
  };

  const onSelectUnit = (flow, data) => {
    setState((prev) => ({
      ...prev,
      staff: data,
      assignee: data?._id,
      flow: flow,
    }));
    setStep(3);
  };

  const onSelectClassUnit = (flow, data) => {
    setState((prev) => ({
      ...prev,
      staff: data,
      assignee: data?._id,
      flow: flow,
    }));
    setStep(3);
  };
  const onGoodsBack = () => {
    if (state.flow === "Individual") {
      setStep(2);
    } else if (state.flow === "Department" || state.flow === "Custom") {
      setStep(6);
    }
    if (state.flow === "Class") {
      setStep(7);
    } else {
    }
  };
  // console.info("safsdvfhjs", state);
  return (
    <>
      {step === 1 ? (
        <IssueRecordToIssuing
          onClose={onClose}
          setStep={setStep}
          state={state}
          setState={setState}
        />
      ) : step === 2 ? (
        <InstituteStaff
          onSelectStaff={onSelectStaff}
          onClose={() => setStep(1)}
          instituteId={instituteId}
        />
      ) : step === 3 ? (
        <MultiSelectGoods
          onClose={onGoodsBack}
          stid={stid}
          onGoodsSelect={onGoodsSelect}
          dataState={state}
        />
      ) : step === 4 ? (
        <IssueRecordForm
          onClose={() => setStep(3)}
          state={state}
          setStep={setStep}
          setState={setState}
        />
      ) : step === 5 ? (
        <StoreIssueRecordConfirm
          onClose={() => setStep(4)}
          state={state}
          onParentClose={onClose}
          stid={stid}
        />
      ) : step === 6 ? (
        <SelectStoreUnit
          onClose={() => setStep(1)}
          stid={stid}
          onSelectUnit={onSelectUnit}
          selectedId={state.assignee ? [state.assignee] : []}
          setStep={setStep}
          dataState={setState}
        />
      ) : step === 7 ? (
        <SelectStoreDepartmentClass
          onClose={() => setStep(6)}
          did={state.did}
          onSelectUnit={onSelectClassUnit}
          selectedId={state.assignee ? [state.assignee] : []}
        />
      ) : null}
    </>
  );
};

export default StoreIssueRecord;
