import { useTranslation } from "react-i18next";
import style from "../StoreManager.module.css";
import dayjs from "dayjs";
import { localTimeFormat } from "../../../Utils/Functions/localTime";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useState } from "react";

const StoreRequestCard = ({ request }) => {
  const { t } = useTranslation();
  const [step, setStep] = useState("");
  const onClose = () => {
    setStep("");
  };
  return (
    <>
      <div
        className={style.smc_card}
        style={{
          flexDirection: "column",
          alignItems: "flex-start",
        }}
      >
        <div className={style.smccc_inner}>
          <div
            className={style.smccc_inner}
            style={{
              flexDirection: "column",
              alignItems: "flex-start",
              gap: "0.2rem",
              width: "auto",
            }}
          >
            <h6>
              {t("quantity")} {": "}
              {request?.goods?.length}
            </h6>
            <h6>
              {t("date_and_time")} {": "}
              {dayjs(request?.created_at).format("DD/MM/YYYY")}
              {", "}
              {localTimeFormat(request?.created_at, "LT")}
            </h6>
          </div>
          <div
            className={style.smccc_inner}
            style={{
              justifyContent: "flex-end",
              width: "auto",
            }}
          >
            <CreateButton
              label={t("approve")}
              customStyle={{
                paddingBlock: "0.4rem",
                margin: "0",
              }}
              onAction={() => setStep(1)}
            />
            <CreateButton
              label={t("reject")}
              customStyle={{
                paddingBlock: "0.4rem",
                margin: "0",
              }}
              onAction={() => setStep(2)}
            />
          </div>
        </div>
        {request?.goods?.map((gd) => (
          <div className={style.smccc_inner} key={gd?._id}>
            <p>
              {t("name")} {": "}
              {gd?.good?.goods_name ?? ""}
            </p>
            <p>
              {t("quantity")} {": "}
              {gd?.quantity ?? ""}
            </p>
          </div>
        ))}

        <div
          className={style.smccc_inner}
          style={{
            marginTop: "0.5rem",
          }}
        >
          {request?.request_by_custom ? (
            <h6>
              {t("request_by")} {": "}
              {request?.request_by_custom?.good_head_name ?? ""}
              <span>
                -{" "}
                {`${
                  request?.request_by_custom?.good_head_person
                    ?.staffFirstName ?? ""
                } ${
                  request?.request_by_custom?.good_head_person
                    ?.staffMiddleName ?? ""
                } ${
                  request?.request_by_custom?.good_head_person?.staffLastName ??
                  ""
                }`}
              </span>
            </h6>
          ) : request?.request_by_individual ? (
            <h6>
              {t("request_by")} {": "}
              {`${request?.request_by_individual?.staffFirstName ?? ""} ${
                request?.request_by_individual?.staffMiddleName ?? ""
              } ${request?.request_by_individual?.staffLastName ?? ""}`}
            </h6>
          ) : null}
        </div>
      </div>

      {/* {open && <StoreIssueGoods irid={issue?._id} onClose={onToggle} />} */}
    </>
  );
};

export default StoreRequestCard;
