import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import style from "../../../FinanceManager/FinanceManager.module.css";
import { useLocation } from "react-router-dom";
import StoreMasterCategoryCard from "./StoreMasterCategoryCard";
import AddStoreMasterSubCategory from "./AddStoreMasterSubCategory";
import { useStoreMasterSubCategoryList } from "../../../../Hooks/ApiHooks/StoreManager/store-manager-api";

const StoreMasterCategory = () => {
  const getQuery = useLocation();
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const [addCategory, setAddCategory] = useState(false);

  const {
    storeMasterSubCategoryList,
    storeMasterSubCategoryListLoading,
    storeMasterSubCategoryListRefetch,
  } = useStoreMasterSubCategoryList({
    data: {
      cid: getQuery.state?.category?._id,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !getQuery.state?.category?._id,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (storeMasterSubCategoryList?.list?.length) {
      let obj = nextPage(storeMasterSubCategoryList?.list?.length);
      setState(() => obj);
    }
  }, [storeMasterSubCategoryList?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onToggle = () => {
    setAddCategory((pre) => !pre);
  };
  const onRefetch = () => {
    setPage(() => 1);
    setSearch("");
    storeMasterSubCategoryListRefetch();
  };

  return (
    <>
      <UserMemberContentWrapper
        customStyle={{
          padding: "0.8rem 0 0 0",
        }}
      >
        <div className={style.fm_header_container}>
          <h6>{getQuery.state?.category?.category_name ?? ""}</h6>
          <div className={style.fm_search}>
            <section className={style.fm_search_container}>
              <div className={style.fm_search_container_inner}>
                <img
                  className={style.fm_search_icon}
                  alt="search icon"
                  src={`${assestsNavbarUrl}/navbar-search.svg`}
                />
                <input
                  type="text"
                  placeholder={t("search")}
                  onChange={onSearchEvent}
                  style={{
                    minWidth: "270px",
                  }}
                />
              </div>
            </section>
            <CreateButton
              label="add_goods"
              customStyle={{
                paddingBlock: "0.4rem",
                margin: "0",
              }}
              onAction={onToggle}
            />
          </div>
        </div>

        {storeMasterSubCategoryList?.list?.map((subCategory, index) =>
          storeMasterSubCategoryList?.list?.length === index + 1 ? (
            <div key={subCategory?._id} ref={ref}>
              <StoreMasterCategoryCard subCategory={subCategory} />
            </div>
          ) : (
            <StoreMasterCategoryCard
              subCategory={subCategory}
              key={subCategory?._id}
            />
          )
        )}
        {storeMasterSubCategoryListLoading ? (
          search ? (
            <QvipleLoader />
          ) : (
            <Loading />
          )
        ) : search ? (
          storeMasterSubCategoryList?.list?.length === 0 && (
            <EmptyMessage title="No goods found related this search." />
          )
        ) : (
          storeMasterSubCategoryList?.list?.length === 0 && (
            <EmptyMessage title="No goods." />
          )
        )}

        {addCategory && (
          <AddStoreMasterSubCategory
            onRefetch={onRefetch}
            onClose={onToggle}
            cid={getQuery.state?.category?._id}
          />
        )}
      </UserMemberContentWrapper>
    </>
  );
};

export default StoreMasterCategory;
