import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import { assestsUtilityUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../FinanceManager/FinanceManager.module.css";
import { useTranslation } from "react-i18next";
import StoreSevenTab from "../StoreSevenTab";
import StoreSevenRegister from "../StoreSevenRegister/StoreSevenRegister";
import StoreSevenMaintenance from "../StoreSevenMaintenance/StoreSevenMaintenance";
import StoreSevenRequest from "../StoreSevenRequest/StoreSevenRequest";
import StoreSevenIssue from "../StoreSevenIssue/StoreSevenIssue";
import StoreSevenStocktake from "../StoreSevenStocktake/StoreSevenStocktake";
import StoreSevenConsumption from "../StoreSevenConsumption/StoreSevenConsumption";
import StoreSevenReturn from "../StoreSevenReturn/StoreSevenReturn";
const StoreSevenUnitCustomConfig = () => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const navigate = useNavigate();
  const params = useParams();
  const [searchParams] = useSearchParams();
  const onBack = () => {
    navigate(`/q/${params.username}/member/staff/store`, {
      state: {
        ...getQuery.state,
        unit: "",
      },
    });
  };
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <div className={style.fm_header_start_container}>
              <img
                alt="search icon"
                src={`${assestsUtilityUrl}/back.svg`}
                style={{
                  height: "2rem",
                  width: "2rem",
                }}
                onClick={onBack}
              />
              <h6>
                {getQuery.state?.unit?.good_head_name ?? ""} -{" "}
                {getQuery.state?.unit?.good_title_person ?? ""} ({t("store")})
              </h6>
            </div>
          </div>
        </UserMemberContentWrapper>

        <StoreSevenTab
          inititalUrl={searchParams.get("type")}
          parentState={getQuery.state}
        />
        <UserMemberContentWrapper>
          {searchParams.get("n") === "request" ? (
            <StoreSevenRequest flow="Custom" mid={getQuery.state?.unit?._id} />
          ) : searchParams.get("n") === "issue" ? (
            <StoreSevenIssue flow="Custom" mid={getQuery.state?.unit?._id} />
          ) : searchParams.get("n") === "stocktake" ? (
            <StoreSevenStocktake
              flow="Custom"
              mid={getQuery.state?.unit?._id}
            />
          ) : searchParams.get("n") === "consumption" ? (
            <StoreSevenConsumption
              flow="Custom"
              mid={getQuery.state?.unit?._id}
            />
          ) : searchParams.get("n") === "return" ? (
            <StoreSevenReturn flow="Custom" mid={getQuery.state?.unit?._id} />
          ) : searchParams.get("n") === "maintenance" ? (
            <StoreSevenMaintenance
              flow="Custom"
              mid={getQuery.state?.unit?._id}
            />
          ) : (
            <StoreSevenRegister flow="Custom" mid={getQuery.state?.unit?._id} />
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
    </>
  );
};

export default StoreSevenUnitCustomConfig;
