import { Link, useLocation, useParams } from "react-router-dom";
import style from "../StoreManager.module.css";
import { useTranslation } from "react-i18next";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useState } from "react";

const StoreMasterCard = ({ category }) => {
  const { t } = useTranslation();
  const params = useParams();
  const getQuery = useLocation();
  const [step, setStep] = useState("");

  const onMenu = (e) => {
    e.preventDefault();
    setStep(1);
  };
  return (
    <>
      <Link
        to={`/q/${params.username}/member/staff/store?type=masters&n=category`}
        state={{
          ...getQuery.state,
          category: category,
        }}
      >
        <div className={style.smc_card}>
          <div className={style.smc_card_contatiner}>
            <img
              src={`${assestsUserFinanceUrl}/category.svg`}
              alt="categor icon"
            />
            <div className={style.smc_card_contatiner_inner}>
              <h6>
                {t("category_name")}
                {": "}
                {category?.category_name ?? ""}
              </h6>
              <p>
                {t("category_type")} {": "}{" "}
                {category?.category_type === "FIXED_ASSESTS"
                  ? "Fixed Assests"
                  : "Inventory"}
              </p>
            </div>
          </div>
          <img
            src={`${assestsUserFinanceUrl}/menu.svg`}
            alt="menu icon"
            onClick={onMenu}
            className={style.menu_icon}
            title="Menu"
          />
        </div>
      </Link>
    </>
  );
};

export default StoreMasterCard;
