import { useState } from "react";
import {
  assestsFeedUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import Modal from "../../../Ui/Modal/Modal";
import { useTranslation } from "react-i18next";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import FormInput from "../../../Ui/Input/FormInput";
import CreateButton from "../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../Loader/QvipleLoader";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import style from "../../FinanceManager/Income/Income.module.css";
import { useStoreUnitAddHeadPerson } from "../../../Hooks/ApiHooks/StoreManager/store-manager-api";
import InstituteStaff from "../../InstituteStaff/InstituteStaff";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";

const AddStoreUnit = ({ onClose, onRefetch, stid, instituteId }) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    good_head_name: "",
    good_title_person: "",
    good_head_person: "",
  });
  const [staff, setStaff] = useState("");
  const [step, setStep] = useState(1);

  const [storeUnitAddHeadPerson] = useStoreUnitAddHeadPerson();

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };
  const onSelectStaff = (val) => {
    setStaff(val);
    setState((prev) => ({
      ...prev,
      good_head_person: val?._id,
    }));
    setStep(1);
  };
  const onAddHeadPerson = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation) && stid) {
      setDisabled((pre) => !pre);
      storeUnitAddHeadPerson({
        stid: stid,
        bodyParameter: state,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };

  //   console.info("stid", stid);
  return (
    <>
      {step === 3 ? (
        <InstituteStaff
          onSelectStaff={onSelectStaff}
          onClose={() => setStep(1)}
          instituteId={instituteId}
        />
      ) : (
        <Modal onClose={onClose}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t("add_new_head_person")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <FormInput
                label={t("good_head_name")}
                placeholder={t("good_head_name_placeholder")}
                name={"good_head_name"}
                value={state.good_head_name}
                type={"text"}
                onChange={onChange}
                errorShow={errorField["good_head_name"]}
              />

              <div
                className={style.card_container_image}
                onClick={() => setStep(3)}
                style={{
                  cursor: "pointer",
                  marginTop: "0.8rem",
                }}
              >
                <img
                  alt="not found"
                  src={
                    staff?.staffProfilePhoto
                      ? `${imageShowUrl}/${staff.staffProfilePhoto}`
                      : `${assestsFeedUrl}/feed-user-avatar.svg`
                  }
                  style={{
                    width: "3rem",
                    height: "3rem",
                  }}
                />
                <div className={style.card_container_inner}>
                  {!staff?.staffFirstName ? (
                    <>
                      <h6>{t("assign_head")}</h6>
                      <p>{t("search_from_joined_list")}</p>
                    </>
                  ) : (
                    <>
                      <h6>{`${staff?.staffFirstName ?? ""} 
                    ${staff?.staffMiddleName ?? ""}
                    ${staff?.staffLastName ?? ""}`}</h6>
                      <p>
                        {t("index_number_1")} {staff?.staffROLLNO ?? ""}
                      </p>
                    </>
                  )}
                </div>
              </div>

              <FormInput
                label={t("good_title_person")}
                placeholder={t("good_title_person_placeholder")}
                name={"good_title_person"}
                value={state.good_title_person}
                type={"text"}
                onChange={onChange}
                errorShow={errorField["good_title_person"]}
              />

              <CreateButton label="create" onAction={onAddHeadPerson} />
            </UserMemberContentWrapper>
            {disabled && <QvipleLoader />}
          </div>
        </Modal>
      )}
    </>
  );
};

export default AddStoreUnit;
