import { useState } from "react";
import SelectGoods from "./SelectGoods";
import AddStockForm from "./AddStockForm";
import AddStoreStockPreview from "./AddStoreStockPreview";

const AddStoreStock = ({ onClose, stid }) => {
  const [step, setStep] = useState(1);
  const [state, setState] = useState({
    goods: "",
    goods_volume: "",
    goods_price: "",
  });

  const onGoodsSelect = (data) => {
    setState((prev) => ({
      ...prev,
      goods: data,
    }));
    setStep(2);
  };
  return (
    <>
      {step === 1 ? (
        <SelectGoods
          onClose={onClose}
          stid={stid}
          onGoodsSelect={onGoodsSelect}
          selectedGoods={state.goods?._id ? [state.goods?._id] : []}
        />
      ) : step === 2 ? (
        <AddStockForm
          setState={setState}
          state={state}
          onClose={() => setStep(1)}
          setStep={setStep}
        />
      ) : step === 3 ? (
        <AddStoreStockPreview
          onClose={() => setStep(2)}
          onParentClose={onClose}
          stid={stid}
          state={state}
        />
      ) : null}
    </>
  );
};

export default AddStoreStock;
