import { useState } from "react";
import CreateButton from "../../../../Ui/Button/CreateButton";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../FinanceManager/Income/Income.module.css";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import FormInput from "../../../../Ui/Input/FormInput";
import { useTranslation } from "react-i18next";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import SelectGoodsCard from "./SelectGoodsCard";
const AddStockForm = ({ onClose, state, setState, setStep }) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");

  const onNext = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation)) {
      setStep(3);
    } else {
      setErrorField(validation);
    }
  };

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };
  return (
    <Modal onClose={onClose}>
      <div className={style.modal_container}>
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>{t("add_stock")}</h6>
            </div>
            <img
              src={`${assestsUserFinanceUrl}/close.svg`}
              alt="close icon"
              onClick={onClose}
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom
          customStyleBorder={{
            marginBottom: "0",
          }}
        />
        <UserMemberContentWrapper
          customStyle={{
            paddingBottom: "1rem",
          }}
        >
          <SelectGoodsCard goods={state?.goods} isRead />

          <FormInput
            label={t("goods_quantity")}
            placeholder={t("goods_quantity_placeholder")}
            name={"goods_volume"}
            value={state?.goods_volume}
            type={"text"}
            onChange={onChange}
            errorShow={errorField["goods_volume"]}
          />

          <FormInput
            label={t("price_per_unit")}
            placeholder={t("price_per_unit_placeholder")}
            name={"goods_price"}
            value={state?.goods_price}
            type={"text"}
            onChange={onChange}
            errorShow={errorField["goods_price"]}
          />
          <CreateButton label="preview" onAction={onNext} />
        </UserMemberContentWrapper>
      </div>
    </Modal>
  );
};

export default AddStockForm;
