import { useTranslation } from "react-i18next";
import Loading from "../../../../Loader/Loading";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import style from "../../../PlateformUser/PlateformUser.module.css";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import SelectGoodsCard from "./SelectGoodsCard";
import { useStoreAllGoodsList } from "../../../../Hooks/ApiHooks/StoreManager/store-manager-api";
const SelectGoods = ({ onClose, stid, onGoodsSelect, selectedGoods }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const { storeAllGoodsList, storeAllGoodsListLoading } = useStoreAllGoodsList({
    data: {
      stid: stid,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !stid,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (storeAllGoodsList?.list?.length) {
      let obj = nextPage(storeAllGoodsList?.list?.length);
      setState(() => obj);
    }
  }, [storeAllGoodsList?.list?.length]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };
  const onSelect = (gt) => {
    onGoodsSelect(gt);
  };
  return (
    <Modal onClose={onClose}>
      <div className={style.modal_container}>
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>{t("select_goods")}</h6>
            </div>
            <img
              src={`${assestsUserFinanceUrl}/close.svg`}
              alt="close icon"
              onClick={onClose}
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom
          customStyleBorder={{
            marginBottom: "0",
          }}
        />
        <UserMemberContentWrapper
          customStyle={{
            paddingBottom: "1rem",
          }}
        >
          <div
            className={style.assign_search_container}
            style={{
              marginBottom: "1rem",
            }}
          >
            <input
              type="text"
              placeholder={t("search")}
              className={style.assign_search_input}
              onChange={onSearchEvent}
              style={{
                width: "100%",
              }}
            />
            <img
              src={`${assestsNavbarUrl}/navbar-search.svg`}
              alt="search icon"
            />
          </div>

          {storeAllGoodsList?.list?.map((goods, index) =>
            storeAllGoodsList?.list?.length === index + 1 ? (
              <div key={goods?._id} ref={ref}>
                <SelectGoodsCard
                  goods={goods}
                  selectedGoods={selectedGoods}
                  onSelect={onSelect}
                />
              </div>
            ) : (
              <SelectGoodsCard
                goods={goods}
                key={goods?._id}
                selectedGoods={selectedGoods}
                onSelect={onSelect}
              />
            )
          )}
          {storeAllGoodsListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            storeAllGoodsList?.list?.length === 0 && (
              <EmptyMessage
                title="No goods found related this search."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          ) : (
            storeAllGoodsList?.list?.length === 0 && (
              <EmptyMessage
                title="No goods."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          )}
        </UserMemberContentWrapper>
      </div>
    </Modal>
  );
};

export default SelectGoods;
