import { useSearchParams } from "react-router-dom";
import StoreIssue from "./StoreIssue";
import StoreRequest from "./StoreRequest";
const StoreIssueConfig = ({ stid, instituteId }) => {
  const [searchParams] = useSearchParams();

  return (
    <>
      {searchParams.get("n") === "records" ? (
        <StoreIssue stid={stid} instituteId={instituteId} />
      ) : (
        <StoreRequest stid={stid} instituteId={instituteId} />
      )}
    </>
  );
};

export default StoreIssueConfig;
