import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import { assestsUtilityUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../FinanceManager/FinanceManager.module.css";
import { useTranslation } from "react-i18next";
import StoreSevenTab from "../StoreSevenTab";
import StoreGoodsRegister from "../StoreGoodsRegister/StoreGoodsRegister";
import StoreGoodsMaintenance from "../StoreGoodsMaintenance/StoreGoodsMaintenance";
import StoreGoodsRequest from "../StoreGoodsRequest/StoreGoodsRequest";
import StoreGoodsIssue from "../StoreGoodsIssue/StoreGoodsIssue";
import StoreGoodsStocktake from "../StoreGoodsStocktake/StoreGoodsStocktake";
import StoreGoodsConsumption from "../StoreGoodsConsumption/StoreGoodsConsumption";
import StoreGoodsReturn from "../StoreGoodsReturn/StoreGoodsReturn";
const StoreSevenStockGoodsConfig = () => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const navigate = useNavigate();
  const params = useParams();
  const [searchParams] = useSearchParams();
  const onBack = () => {
    navigate(`/q/${params.username}/member/staff/store`, {
      state: {
        ...getQuery.state,
        goods: null,
      },
    });
  };
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <div className={style.fm_header_start_container}>
              <img
                alt="search icon"
                src={`${assestsUtilityUrl}/back.svg`}
                style={{
                  height: "2rem",
                  width: "2rem",
                }}
                onClick={onBack}
              />
              <h6>{getQuery.state?.goods?.goods_name ?? ""}</h6>
            </div>
          </div>
        </UserMemberContentWrapper>

        <StoreSevenTab
          inititalUrl={searchParams.get("type")}
          parentState={getQuery.state}
        />
        <UserMemberContentWrapper>
          {searchParams.get("n") === "request" ? (
            <StoreGoodsRequest gid={getQuery.state?.goods?._id} />
          ) : searchParams.get("n") === "issue" ? (
            <StoreGoodsIssue gid={getQuery.state?.goods?._id} />
          ) : searchParams.get("n") === "stocktake" ? (
            <StoreGoodsStocktake gid={getQuery.state?.goods?._id} />
          ) : searchParams.get("n") === "consumption" ? (
            <StoreGoodsConsumption gid={getQuery.state?.goods?._id} />
          ) : searchParams.get("n") === "return" ? (
            <StoreGoodsReturn gid={getQuery.state?.goods?._id} />
          ) : searchParams.get("n") === "maintenance" ? (
            <StoreGoodsMaintenance gid={getQuery.state?.goods?._id} />
          ) : (
            <StoreGoodsRegister gid={getQuery.state?.goods?._id} />
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
    </>
  );
};

export default StoreSevenStockGoodsConfig;
