import { useState } from "react";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import Modal from "../../../../Ui/Modal/Modal";
import { useTranslation } from "react-i18next";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import FormInput from "../../../../Ui/Input/FormInput";
import CreateButton from "../../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import CustomAutomateSelect from "../../../../Ui/Select/CustomAutomateSelect";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import style from "../../../FinanceManager/Income/Income.module.css";
import FormFile from "../../../../Ui/Input/FormFile";
import { useStoreMasterAddSubCategory } from "../../../../Hooks/ApiHooks/StoreManager/store-manager-api";
import { useFileUpload } from "../../../../../hooks/authentication-api";
const AddStoreMasterSubCategory = ({ onClose, onRefetch, cid }) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    goods_name: "",
    goods_quantity: "",
    good_icon: "",
  });

  const [storeMasterAddSubCategory] = useStoreMasterAddSubCategory();
  const [fileUpload] = useFileUpload();

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onSelectType = (data) => {
    setState((prev) => ({
      ...prev,
      goods_quantity: data?.name,
    }));
  };

  const onFileUpload = (e) => {
    if (e.target.files?.length) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          if (res.data?.imageKey) {
            setState((prev) => ({
              ...prev,
              good_icon: res.data?.imageKey,
            }));
            setDisabled((pre) => !pre);
          }
        })
        .catch({});
    }
  };

  const onAddSubCategory = () => {
    let validation = requiredField(state, ["good_icon"]);
    if (existingRequiredField(validation) && cid) {
      setDisabled((pre) => !pre);
      storeMasterAddSubCategory({
        cid: cid,
        bodyParameter: state,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };

  // console.info("sdfsfg", state);
  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("add_goods")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <FormInput
              label={t("goods_name")}
              placeholder={t("goods_name_placeholder")}
              name={"goods_name"}
              value={state.goods_name}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["goods_name"]}
            />

            <CustomAutomateSelect
              selectLabel={t("measures")}
              selectedValue={t("please_select_measures")}
              onClick={onSelectType}
              options={[
                {
                  name: "Quantity",
                },
                {
                  name: "Pieces",
                },
                {
                  name: "Kg",
                },
                {
                  name: "Gram",
                },
                {
                  name: "Liter",
                },
                {
                  name: "Meter",
                },
              ]}
              defalutValue={state.goods_quantity}
              errorShow={errorField["goods_quantity"]}
            />
            <FormFile
              label={t("icon_upload")}
              name={"icicon_uploadon"}
              value={
                state.good_icon
                  ? {
                      key: state.good_icon,
                    }
                  : ""
              }
              onChange={onFileUpload}
            />
            <CreateButton label="create" onAction={onAddSubCategory} />
          </UserMemberContentWrapper>
          {disabled && <QvipleLoader />}
        </div>
      </Modal>
    </>
  );
};

export default AddStoreMasterSubCategory;
