import { Link, useLocation, useParams } from "react-router-dom";
import { imageShowUrl } from "../../../../Services/UrlConfig/BaseUrl";
import { assestsFeedUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../StoreManager.module.css";

const StoreUnitDepartmentDetailCard = ({ cls }) => {
  const getQuery = useLocation();
  const params = useParams();
  return (
    <>
      <Link
        to={`/q/${params.username}/member/staff/store?type=store-cls`}
        state={{
          ...getQuery,
          clsId: cls?._id,
          clsName: `${cls?.className} - ${cls?.classTitle}`,
        }}
      >
        <div className={style.smc_card}>
          <div
            className={style.smc_card_contatiner}
            style={{
              justifyContent: "space-between",
              width: "100%",
            }}
          >
            <div className={style.smc_card_contatiner}>
              <img
                src={
                  cls?.classTeacher?.staffProfilePhoto
                    ? `${imageShowUrl}/${cls?.classTeacher?.staffProfilePhoto}`
                    : `${assestsFeedUrl}/feed-user-avatar.svg`
                }
                loading="lazy"
                alt="avatar"
                style={{
                  height: "2.3rem",
                  width: "2.3rem",
                  borderRadius: "50%",
                }}
              />

              <div className={style.smc_card_contatiner_inner}>
                <h6>
                  {cls?.className ?? ""} - {cls?.classTitle ?? ""}
                </h6>
                <p>
                  {`${cls?.classTeacher?.staffFirstName ?? ""} ${
                    cls?.classTeacher?.staffMiddleName ?? ""
                  } ${cls?.classTeacher?.staffLastName ?? ""}`}
                </p>
              </div>
            </div>
          </div>
        </div>
      </Link>
    </>
  );
};

export default StoreUnitDepartmentDetailCard;
