import { useState } from "react";
import CreateButton from "../../../../Ui/Button/CreateButton";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../FinanceManager/Income/Income.module.css";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import { useTranslation } from "react-i18next";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import FormInputView from "../../../../Ui/Input/FormInputView";
import SelectGoodsCard from "./SelectGoodsCard";
import { useStoreStockGoodsUpdate } from "../../../../Hooks/ApiHooks/StoreManager/store-manager-api";
const AddStoreStockPreview = ({ onClose, state, stid, onParentClose }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);

  const [storeStockGoodsUpdate] = useStoreStockGoodsUpdate();
  const onAddStock = () => {
    if (stid && state?.goods?._id) {
      setDisabled((pre) => !pre);
      storeStockGoodsUpdate({
        gid: state?.goods?._id,
        bodyParameter: {
          sid: stid,
          goods_volume: +state?.goods_volume,
          goods_price: +state?.goods_price,
        },
      })
        .then(() => {
          setDisabled((pre) => !pre);
          onParentClose();
        })
        .catch({});
    }
  };
  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("add_stock_preview")}</h6>
              </div>

              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom
            customStyleBorder={{
              marginBottom: "0",
            }}
          />
          <UserMemberContentWrapper
            customStyle={{
              paddingBottom: "1rem",
            }}
          >
            <SelectGoodsCard goods={state?.goods} isRead />
            <FormInputView
              label={t("goods_quantity")}
              value={state?.goods_volume}
            />
            <FormInputView
              label={t("price_per_unit")}
              value={state?.goods_price}
            />
            <CreateButton label="confirm_and_save" onAction={onAddStock} />
          </UserMemberContentWrapper>
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default AddStoreStockPreview;
