import { Link, useLocation, useParams } from "react-router-dom";
import { imageShowUrl } from "../../../../Services/UrlConfig/BaseUrl";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../StoreManager.module.css";

const StoreUnitDepartmentCard = ({ depart }) => {
  const getQuery = useLocation();
  const params = useParams();
  return (
    <>
      <Link
        to={`/q/${params.username}/member/staff/store?type=unit&n=detail`}
        state={{
          ...getQuery.state,
          did: depart?._id,
          name: depart?.dName,
        }}
      >
        <div className={style.smc_card}>
          <div
            className={style.smc_card_contatiner}
            style={{
              justifyContent: "space-between",
              width: "100%",
            }}
          >
            <div className={style.smc_card_contatiner}>
              <img
                src={
                  depart?.dHead?.staffProfilePhoto
                    ? `${imageShowUrl}/${depart?.dHead?.staffProfilePhoto}`
                    : `${assestsUserFinanceUrl}/department.svg`
                }
                loading="lazy"
                alt="avatar"
                style={{
                  height: "2.3rem",
                  width: "2.3rem",
                  borderRadius: "50%",
                }}
              />

              <div className={style.smc_card_contatiner_inner}>
                <h6>{depart?.dName ?? ""}</h6>
                <p>
                  {`${depart?.dHead?.staffFirstName ?? ""} ${
                    depart?.dHead?.staffMiddleName ?? ""
                  } ${depart?.dHead?.staffLastName ?? ""}`}
                </p>
              </div>
            </div>
          </div>
        </div>
      </Link>
    </>
  );
};

export default StoreUnitDepartmentCard;
