import { useTranslation } from "react-i18next";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../FinanceManager/Income/Income.module.css";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import TileWrapper from "../../../../Ui/Tab/Tile/TileWrapper";
import TileCard from "../../../../Ui/Tab/Tile/TileCard";
import CreateButton from "../../../../Ui/Button/CreateButton";
const IssueRecordToIssuing = ({ setState, state, onClose, setStep }) => {
  const { t } = useTranslation();

  const onNext = () => {
    if (state?.issue_to === "Unit") {
      setStep(6);
    } else if (state?.issue_to === "Individual") {
      setStep(2);
    } else {
    }
  };

  const onTile = (value) => {
    setState((prev) => ({
      ...prev,
      issue_to: value,
    }));
  };

  return (
    <Modal onClose={onClose}>
      <div className={style.modal_container}>
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>{t("issues_record")}</h6>
            </div>

            <img
              src={`${assestsUserFinanceUrl}/close.svg`}
              alt="close icon"
              onClick={onClose}
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom
          customStyleBorder={{
            marginBottom: "0",
          }}
        />
        <UserMemberContentWrapper
          customStyle={{
            paddingBottom: "1rem",
          }}
        >
          <TileWrapper label="issuing_to">
            {issuingTo?.map((tile) => (
              <TileCard
                key={tile?.key}
                tile={tile}
                onTile={onTile}
                activeTile={state?.issue_to}
              />
            ))}
          </TileWrapper>

          <CreateButton label="next" onAction={onNext} />
        </UserMemberContentWrapper>
      </div>
    </Modal>
  );
};

export default IssueRecordToIssuing;

export const issuingTo = [
  {
    key: "Unit",
    name: "unit",
  },
  {
    key: "Individual",
    name: "individual",
  },
];
