import { useInstituteAllDepartment } from "../../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
// import { useTranslation } from "react-i18next";
import StoreUnitDepartmentCard from "./StoreUnitDepartmentCard";
const StoreUnitDepartment = ({ fid }) => {
  // const { t } = useTranslation();

  const { instituteAllDepartment } = useInstituteAllDepartment({
    fid: fid,
    skip: !fid,
  });

  return (
    <>
      {instituteAllDepartment?.map((depart) => (
        <StoreUnitDepartmentCard depart={depart} key={depart?._id} />
      ))}
    </>
  );
};

export default StoreUnitDepartment;

