import { useLocation } from "react-router-dom";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import StoreIssueWrapper from "./StoreIssueWrapper";
import { useTranslation } from "react-i18next";
import style from "../../FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useStoreStockIssueRequestList } from "../../../Hooks/ApiHooks/StoreManager/store-manager-api";
import StoreRequestCard from "./StoreRequestCard";
import StoreIssueRecord from "./StoreIssueRecord/StoreIssueRecord";
const StoreRequest = ({ stid, instituteId }) => {
  const getQuery = useLocation();
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const [add, setAdd] = useState(false);

  const { storeStockIssueRequestList, storeStockIssueRequestListLoading } =
    useStoreStockIssueRequestList({
      data: {
        stid: stid,
        page: page,
        limit: 10,
        search: search,
      },
      skip: !stid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (storeStockIssueRequestList?.list?.length) {
      let obj = nextPage(storeStockIssueRequestList?.list?.length);
      setState(() => obj);
    }
  }, [storeStockIssueRequestList?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  // console.info("sdffsg", storeStockIssueRequestList);
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("store_issue")}</h6>
            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                  />
                </div>
              </section>

              <CreateButton
                label="issue_record"
                customStyle={{
                  paddingBlock: "0.4rem",
                  margin: "0",
                }}
                onAction={onToggle}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <StoreIssueWrapper parentState={getQuery.state} />
        <UserMemberContentWrapper>
          {storeStockIssueRequestList?.list?.map((request, index) =>
            storeStockIssueRequestList?.list?.length === index + 1 ? (
              <div key={request?._id} ref={ref}>
                <StoreRequestCard request={request} />
              </div>
            ) : (
              <StoreRequestCard request={request} key={request?._id} />
            )
          )}
          {storeStockIssueRequestListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            storeStockIssueRequestList?.list?.length === 0 && (
              <EmptyMessage title="No request found related this search." />
            )
          ) : (
            storeStockIssueRequestList?.list?.length === 0 && (
              <EmptyMessage title="No request." />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {add && (
        <StoreIssueRecord
          instituteId={instituteId}
          stid={stid}
          onClose={onToggle}
        />
      )}
    </>
  );
};

export default StoreRequest;
