import { useTranslation } from "react-i18next";
import dayjs from "dayjs";
import style from "../../StoreManager.module.css";
import { localTimeFormat } from "../../../../Utils/Functions/localTime";
const StoreSevenIssueCard = ({ issue }) => {
  const { t } = useTranslation();
  return (
    <>
      <div
        className={style.smc_card}
        style={{
          flexDirection: "column",
          alignItems: "flex-start",
        }}
      >
        <div className={style.smccc_inner}>
          <h6>
            {t("quantity")} {": "}
            {issue?.goods?.length}
          </h6>
          <h6>
            {t("date_and_time")} {": "}
            {dayjs(issue?.created_at).format("DD/MM/YYYY")}
            {", "}
            {localTimeFormat(issue?.created_at, "LT")}
          </h6>
        </div>
        {issue?.goods?.map((gd) => (
          <div className={style.smccc_inner} key={gd?._id}>
            <p>
              {t("name")} {": "}
              {gd?.good?.goods_name ?? ""}
            </p>
            <p>
              {t("quantity")} {": "}
              {gd?.quantity ?? ""}
            </p>
          </div>
        ))}
        <div
          className={style.smccc_inner}
          style={{
            marginTop: "0.5rem",
          }}
        >
          {issue?.issue_to_custom ? (
            <h6>
              {t("issue_to")} {": "}
              {issue?.issue_to_custom?.good_head_name ?? ""}
              <span>
                -{" "}
                {`${
                  issue?.issue_to_custom?.good_head_person?.staffFirstName ?? ""
                } ${
                  issue?.issue_to_custom?.good_head_person?.staffMiddleName ??
                  ""
                } ${
                  issue?.issue_to_custom?.good_head_person?.staffLastName ?? ""
                }`}
              </span>
            </h6>
          ) : null}
        </div>
      </div>
    </>
  );
};

export default StoreSevenIssueCard;
