import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import Modal from "../../../../Ui/Modal/Modal";
import { useStoreDepartmentClassList } from "../../../../Hooks/ApiHooks/StoreManager/store-manager-api";
import SelectClassUnitCard from "./IssueUnit/SelectClassUnitCard";
import style from "../../../PlateformUser/PlateformUser.module.css";
const SelectStoreDepartmentClass = ({
  onClose,
  did,
  onSelectUnit,
  selectedId,
}) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const { storeDepartmentClassList, storeDepartmentClassListLoading } =
    useStoreDepartmentClassList({
      data: {
        did: did,
        page: page,
        limit: 10,
        search: search,
      },
      skip: !did,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (storeDepartmentClassList?.list?.length) {
      let obj = nextPage(storeDepartmentClassList?.list?.length);
      setState(() => obj);
    }
  }, [storeDepartmentClassList?.list?.length]);
  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onSelect = (data) => {
    onSelectUnit("Class", data);
  };
  return (
    <Modal onClose={onClose}>
      <div className={style.modal_container}>
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>{t("select_department_classes")}</h6>
            </div>
            <img
              src={`${assestsUserFinanceUrl}/close.svg`}
              alt="close icon"
              onClick={onClose}
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom
          customStyleBorder={{
            marginBottom: "0",
          }}
        />
        <UserMemberContentWrapper
          customStyle={{
            paddingBottom: "1rem",
          }}
        >
          <div
            className={style.assign_search_container}
            style={{
              marginBottom: "1rem",
            }}
          >
            <input
              type="text"
              placeholder={t("search")}
              className={style.assign_search_input}
              onChange={onSearchEvent}
              style={{
                width: "100%",
              }}
            />
            <img
              src={`${assestsNavbarUrl}/navbar-search.svg`}
              alt="search icon"
            />
          </div>

          {storeDepartmentClassList?.list?.map((cls, index) =>
            storeDepartmentClassList?.list?.length === index + 1 ? (
              <div key={cls?._id} ref={ref}>
                <SelectClassUnitCard
                  cls={cls}
                  onSelect={onSelect}
                  selectedId={selectedId}
                />
              </div>
            ) : (
              <SelectClassUnitCard
                cls={cls}
                key={cls?._id}
                onSelect={onSelect}
                selectedId={selectedId}
              />
            )
          )}
          {storeDepartmentClassListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            storeDepartmentClassList?.list?.length === 0 && (
              <EmptyMessage
                title="No classes found related this search."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          ) : (
            storeDepartmentClassList?.list?.length === 0 && (
              <EmptyMessage
                title="No classes."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          )}
        </UserMemberContentWrapper>
      </div>
    </Modal>
  );
};

export default SelectStoreDepartmentClass;
