import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { useStoreMasterCategoryList } from "../../../Hooks/ApiHooks/StoreManager/store-manager-api";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import style from "../../FinanceManager/FinanceManager.module.css";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import SquareTabWrapper from "../../../Ui/Tab/Square/SquareTabWrapper";
import CustomSquareTabCard from "../../../Ui/Tab/Square/CustomSquareTabCard";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import Loading from "../../../Loader/Loading";
import CreateButton from "../../../Ui/Button/CreateButton";
import AddStoreStock from "./AddStoreStock/AddStoreStock";
const StoreStock = ({ stid }) => {
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const [add, setAdd] = useState(false);

  const { storeMasterCategoryList, storeMasterCategoryListLoading } =
    useStoreMasterCategoryList({
      data: {
        stid: stid,
        page: page,
        limit: 10,
        search: search,
      },
      skip: !stid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (storeMasterCategoryList?.list?.length) {
      let obj = nextPage(storeMasterCategoryList?.list?.length);
      setState(() => obj);
    }
  }, [storeMasterCategoryList?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onToggle = () => {
    setAdd((pre) => !pre);
  };

  const onNavigate = () => {
    navigate(`/q/${params.username}/member/staff/store?type=stock&n=logs`, {
      state: getQuery.state,
    });
  };
  let measure = document.getElementById("589nbsgh")?.clientWidth;
  let height = "9rem";
  let width = "9rem";
  if (measure) {
    let disvible =
      measure > 870 ? (measure > 1100 ? 6 : 5) : measure > 700 ? 4 : 3;
    let mi = measure > 870 ? 1 : 2;
    width = `${Math.floor((measure - mi - 31 * disvible - 1) / disvible)}px`;
    height = width;
  }

  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("stock")}</h6>
            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                  />
                </div>
              </section>
              <img
                src={`${assestsUserFinanceUrl}/history.svg`}
                alt="logs icon"
                onClick={onNavigate}
              />
              <CreateButton
                label="add_stock"
                customStyle={{
                  paddingBlock: "0.4rem",
                  margin: "0",
                }}
                onAction={onToggle}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <SquareTabWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          {storeMasterCategoryList?.list?.map((category, index) =>
            storeMasterCategoryList?.list?.length === index + 1 ? (
              <div key={category?._id} ref={ref}>
                <CustomSquareTabCard
                  label={category?.category_name}
                  iconUrl={`${assestsUserFinanceUrl}/student.svg`}
                  url={`/q/${params.username}/member/staff/store?type=stock&n=category`}
                  state={{ ...getQuery.state, category: category }}
                  customStyleContainer={{
                    width: width,
                    height: height,
                  }}
                />
              </div>
            ) : (
              <CustomSquareTabCard
                label={category?.category_name}
                iconUrl={`${assestsUserFinanceUrl}/student.svg`}
                url={`/q/${params.username}/member/staff/store?type=stock&n=category`}
                state={{ ...getQuery.state, category: category }}
                customStyleContainer={{
                  width: width,
                  height: height,
                }}
              />
            )
          )}
        </SquareTabWrapper>
        {storeMasterCategoryListLoading && <Loading />}
      </UserMemberContainerWrapper>
      {add && <AddStoreStock onClose={onToggle} stid={stid} />}
    </>
  );
};

export default StoreStock;
