import { useState } from "react";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import Modal from "../../../Ui/Modal/Modal";
import { useTranslation } from "react-i18next";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import FormInput from "../../../Ui/Input/FormInput";
import CreateButton from "../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../Loader/QvipleLoader";
import CustomAutomateSelect from "../../../Ui/Select/CustomAutomateSelect";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import style from "../../FinanceManager/Income/Income.module.css";
import { useStoreMasterAddCategory } from "../../../Hooks/ApiHooks/StoreManager/store-manager-api";
const AddStoreMaster = ({ onClose, onRefetch, stid }) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    category_name: "",
    category_type: "",
  });

  const [storeMasterAddCategory] = useStoreMasterAddCategory();

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onSelectType = (data) => {
    setState((prev) => ({
      ...prev,
      category_type: data?.name,
    }));
  };

  const onAddCategory = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation) && stid) {
      setDisabled((pre) => !pre);
      storeMasterAddCategory({
        stid: stid,
        bodyParameter: {
          ...state,
          category_type:
            state.category_type === "Fixed Assests"
              ? "FIXED_ASSESTS"
              : "CONSUMABLES",
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };
  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("add_new_category")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <FormInput
              label={t("category_name")}
              placeholder={t("category_name_placeholder")}
              name={"category_name"}
              value={state.category_name}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["category_name"]}
            />

            <CustomAutomateSelect
              selectLabel={t("category_type")}
              selectedValue={t("please_select_category_type")}
              onClick={onSelectType}
              options={[
                {
                  name: "Inventory",
                },
                {
                  name: "Fixed Assests",
                },
              ]}
              defalutValue={state.category_type}
              errorShow={errorField["category_type"]}
            />
            <CreateButton label="create" onAction={onAddCategory} />
          </UserMemberContentWrapper>
          {disabled && <QvipleLoader />}
        </div>
      </Modal>
    </>
  );
};

export default AddStoreMaster;
