import { useSearchParams } from "react-router-dom";
import StoreReturn from "./StoreReturn";
import StoreReturnRequest from "./StoreReturnRequest";

const StoreReturnConfig = ({ stid, instituteId }) => {
  const [searchParams] = useSearchParams();

  return (
    <>
      {searchParams.get("n") === "records" ? (
        <StoreReturn stid={stid} instituteId={instituteId} />
      ) : (
        <StoreReturnRequest stid={stid} instituteId={instituteId} />
      )}
    </>
  );
};

export default StoreReturnConfig;
