import { Link, useLocation, useParams } from "react-router-dom";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import { assestsFeedUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../StoreManager.module.css";
import { useTranslation } from "react-i18next";
const StoreUnitCard = ({ unit, subUrl, isStatic }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  return (
    <>
      {isStatic ? (
        <Link
          to={`/q/${params.username}/member/staff/store?type=unit&n=${subUrl}`}
          state={getQuery.state}
        >
          <div className={style.smc_card}>
            <div
              className={style.smc_card_contatiner}
              style={{
                justifyContent: "space-between",
                width: "100%",
              }}
            >
              <div className={style.smc_card_contatiner}>
                <img
                  src={
                    unit?.url
                      ? unit?.url
                      : `${assestsFeedUrl}/feed-user-avatar.svg`
                  }
                  loading="lazy"
                  alt="avatar"
                  style={{
                    height: "2.3rem",
                    width: "2.3rem",
                    borderRadius: "50%",
                  }}
                />

                <div className={style.smc_card_contatiner_inner}>
                  <h6>{t(unit?.name)}</h6>
                </div>
              </div>
            </div>
          </div>
        </Link>
      ) : (
        <Link
          to={`/q/${params.username}/member/staff/store?type=store-unit-custom`}
          state={{
            ...getQuery.state,
            unit: unit,
          }}
        >
          <div className={style.smc_card}>
            <div
              className={style.smc_card_contatiner}
              style={{
                justifyContent: "space-between",
                width: "100%",
              }}
            >
              <div className={style.smc_card_contatiner}>
                <img
                  src={
                    unit?.good_head_person?.staffProfilePhoto
                      ? `${imageShowUrl}/${unit?.good_head_person?.staffProfilePhoto}`
                      : `${assestsFeedUrl}/feed-user-avatar.svg`
                  }
                  loading="lazy"
                  alt="avatar"
                  style={{
                    height: "2.3rem",
                    width: "2.3rem",
                    borderRadius: "50%",
                  }}
                />

                <div className={style.smc_card_contatiner_inner}>
                  <h6>
                    {t("good_title_person")} {": "}{" "}
                    {unit?.good_title_person ?? ""}
                    {", "}
                    {t("good_head_name")} {": "} {unit?.good_head_name ?? ""}
                  </h6>

                  <p>
                    {`${unit?.good_head_person?.staffFirstName ?? ""} ${
                      unit?.good_head_person?.staffMiddleName ?? ""
                    } ${unit?.good_head_person?.staffLastName ?? ""}`}
                  </p>
                </div>
              </div>
            </div>
          </div>
        </Link>
      )}
    </>
  );
};

export default StoreUnitCard;
